:root {
    --rapture-tower-box-shadow: 
        600px -550px 250px 1px #092d3f, 
        -600px -550px 250px 1px #092d3f, 
        -150px -150px 250px 10px var(--rapture-sea-green), 
        150px -150px 250px 10px var(--rapture-sea-green), 
        -200px 150px 100px 5px var(--rapture-fluorescent), 
        200px 150px 100px 5px var(--rapture-fluorescent), 
        250px 250px 200px 10px white, 
        -250px 250px 200px 10px white;
    --beacon-shadow: 
        0px 0px 50px 10px var(--rapture-fluorescent), 
        0px 0px 100px 10px var(--rapture-fluorescent), 
        0px 0px 25px var(--rapture-fluorescent);
}

.buildings-container {
    height: 40vh;
    position: absolute;
    width: 100vw;
    bottom: 0;
}
.rapture-main {
    width: 10vh;
    height: 80vh;
    position: absolute;
    background: var(--rapture-sea-green);
    left: calc(50% - 5vh);
    bottom: 0;
    animation: rapture-main-build 3s ease;
}
@keyframes rapture-main-build {
    0% { bottom: -150%; }
    100% { bottom: 0; }
}

.lighthouse-beacon {
    cursor: pointer;
    width: 50%;
    margin-left: 25%;
    position: absolute;
    background: white;
    padding-bottom: 50%;
    margin-top: calc(-20vh + 10px);
    border-radius: 50%;
}
.lighthouse-beacon:hover{
    box-shadow: var(--beacon-shadow);
}

.tower-stacks { position: absolute; }

.tower {
    position: absolute;
    bottom: 0;
    box-shadow: var(--rapture-tower-box-shadow);
    animation-name: rise-up;
    animation-timing-function: ease;
}

@keyframes rise-up {
    0% {
        transform: translateY(150%);
        opacity: 0;
    }
    100% {
        transform: translateY(0%);
        opacity: 1;
    }
}

.fathomless-depths {
    width: 10vw;
    height: 25vh;
    position: absolute;
    bottom: -5%;
    z-index: 6;
    opacity: 0.5;
    left: calc(50% - 5vw);
    background: white;
    background: linear-gradient(180deg, transparent 0%, white 100%);
    cursor: pointer;
}
.fathomless-depths:hover {
    opacity: 1;
}