
.bubbles-container {
    margin-top: -30px;
    z-index: 15;
    position: relative;
}
.bubble {
    border-radius: 100%;
    position: absolute;
    box-shadow: 0 0 20px white, inset 0 0 5px white;
    opacity: 0;
    animation: bubble 15s ease-in-out infinite, sideWays 4s ease-in-out infinite alternate;
}

@keyframes bubble {
    0% {
        transform: translateY(100vh);
        opacity: 0.3;
    }
    90% {
        opacity: 1;
    }
    100% {
        opacity: 0.1;
        transform: translateY(30px);
    }
}
@keyframes sideWays {
    0% {
        margin-left: 0px;
    }
    100% {
        margin-left: 20%;
    }
}
