.about-panel-container {
    margin-top: 12.5vh;
    margin-left: 20px;
    position: absolute;
    z-index: 20;
    font-family: market-deco;
    color: #bbb;
    filter: drop-shadow(2px 4px 6px black);
}
.about-header-title {
    margin: 5px;
    font-size: 2em;
    user-select: none;
}
.arts-musics-container {
    font-size: 1.5em;
    margin: 5px;
    user-select: none;
}
.arts-musics-container > .hobby {
    color: white;
    cursor: pointer;
    text-shadow: 0px 0px 10px white;
}
.arts-musics-container > .hobby:hover {
    text-shadow: 0 0 5px white;
    filter: drop-shadow(2px 4px 6px white);
}
.about-selection-container {
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    width: calc(100vw - 40px);
}
.about-information-panel {
    font-size: 1em;
    display: flex;
    flex-direction: column;
    padding: 10px;
    user-select: none;
}
.selections {
    width: 3em; 
    margin: 0px 20px 5px 5px;
}
.about-selection {
    cursor: pointer;
    margin-top: 10px;
    width: 3em;
    height: 3em;
    border: 5px solid white;
    position: relative;
}
.information-icon-container, .education-icon-container, .experience-icon-container, .skills-icon-container {
    border-radius: 50%;
}
.information-icon-container::after, .education-icon-container::after, .experience-icon-container::after, .skills-icon-container::after {
    height: 100%;
    font-size: 3em;
    color: white;
    width: 100%;
    text-align: center;
    line-height: 1.2em;
    position: absolute;
    font-family: monospace;
}
.information-icon-container::after {
    content: "i";
}
.education-icon-container::after {
    content: "E";
}
.experience-icon-container::after {
    content: "W";
}
.skills-icon-container::after {
    content: "S";
}
.education-url, .experience-content {
    padding: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.education-url:hover {
    color: white;
    cursor: pointer;
}

.experience-content:hover {
    color: #ddd;
    cursor: pointer;
}
.experience-content {
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
}
.experience-sub-details {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    font-family: century-gothic;
    font-size: 1.2rem;
    font-weight: 600;
}

@keyframes slow-show {
    from {
        bottom: 50%;
    }
    to {
        bottom: -50%;
    }
}

.personal-skill-icon > .skill {
    width: 100%;
    height: 100%;
    border-radius: 5%;
    position: relative;
    z-index: 2;
    overflow: hidden;
}
.skills-used-container {
    display: flex;
    align-items: center;
    flex-direction: row;
    margin-top: 5px;
}
.sub-details-container {
    width: 80vw;
    height: 15vh;
    overflow: hidden scroll;
    padding: 10px;
}
.sub-details-container::-webkit-scrollbar {
    width: 10px;
}
.sub-details-container::-webkit-scrollbar-track {
    background: white; 
    border-radius: 10px;
}
.sub-details-container::-webkit-scrollbar-thumb {
    background: black;
    border-radius: 10px;
    border: 3px solid white;
}
.programming-skills-container, .other-skills-container {
    display: flex;
    flex-wrap: wrap;
    width: calc(80vw - 115px - 3em);
}
.skill-description {
    width: 100%;
    font-size: 1.2em;
    text-align: center;
}
.skills-panel-container {
    color: white;
}
.skills-panel {
    display: flex;
    flex-direction: row;
    width: calc(100vw - 75px - 3em);
}
.skill-selector-container {
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: space-around;
    height: 20vh;
    width: 10vw;
    margin-right: 10px;
}
.skill-selector-container > .selector {
    cursor: pointer;
}
.skill-selector-container > .selector:hover {
    scale: 1.2;
}
.proficiency-container {
    height: 10px;
    width: 3em;
    margin: 5px;
    display: flex;
    justify-content: space-around;
    border-radius: 5%;
    overflow: hidden;
}
.proficiency-bar {
    width: 20%;
    height: 100%;
}