.projects-container {
    height: 100vh;
    width: 100vw;
    position: relative;
    background: radial-gradient(circle, rgba(200,61,61,1) 0%, rgba(233,241,132,1) 1%, rgba(255,219,117,1) 3%, rgba(255,175,0,1) 4%, rgba(76,4,149,1) 5%, rgba(49,22,136,1) 15%, rgba(0,0,0,1) 100%);
    overflow: hidden;
    animation: eyeblink 30s infinite;
}
@keyframes eyeblink {
    0%, 1% {
        background: radial-gradient(circle, rgba(200,61,61,1) 0%, rgba(233,241,132,1) 1%, rgba(255,219,117,1) 3%, rgba(255,175,0,1) 4%, rgba(76,4,149,1) 5%, rgba(49,22,136,1) 15%, rgba(0,0,0,1) 100%);
    }
    0.15% {
        background: radial-gradient(circle, rgba(200,61,61,1) 0%, rgba(233,241,132,1) 1%, rgba(255,219,117,1) 3%, rgba(76,4,149,1) 5%, rgba(49,22,136,1) 15%, rgba(0,0,0,1) 100%);
    }
    0.30% {
        background: radial-gradient(circle, rgba(200,61,61,1) 0%, rgba(233,241,132,1) 1%, rgba(76,4,149,1) 5%, rgba(49,22,136,1) 15%, rgba(0,0,0,1) 100%);
    }
    0.45% {
        background: radial-gradient(circle, rgba(200,61,61,1) 0%, rgba(76,4,149,1) 5%, rgba(49,22,136,1) 15%, rgba(0,0,0,1) 100%);
    }
    0.60%,0.75%  {
        background: radial-gradient(circle, rgba(76,4,149,1) 5%, rgba(49,22,136,1) 15%, rgba(0,0,0,1) 100%);
    }
}
