.projects-list-container {
    margin-top: 30px;
    position: absolute;
    z-index: 11;
    font-family: lost-language;
}
.project-container {
    color: white;
    height: 10vh;
    display: flex;
    align-items: center;
    padding: 20px;
    cursor: pointer;
}
.project-text-containter {
    display: flex;
}
.project-title-letter-container {
    position: relative;
}
.project-title {
    display: flex;    
    margin-left: 10px;
    font-size: 2.5em;
}
.project-description {
    display: flex;
    margin-left: 10px;
    font-size: 1.7em;
}
.lost-language-container {
    margin: 2px;
}

.pokemon-icon-container {
    height: 10vh;
    width: 10vh;
    border-radius: 5%;
    background: whitesmoke;
    position: relative;
    overflow: hidden;
}
.pokemon-icon-container::before {
    content: "";
    width: 100%;
    background: #ee1515;
    height: calc(50% - 5px);
    position: absolute;
    border-bottom: 10px solid #222224;
}
.pokemon-icon-container::after {
    content: "";
    width: 50%;
    height: 50%;
    border: 10px solid #222224;
    border-radius: 50%;
    position: absolute;
    top: calc(25% - 10px);
    left: calc(25% - 10px);
    background: whitesmoke;
}
