:root {
    --rain-color: #a1c6cc;
}

.rain-container { position: relative; z-index: 2; }

.raindrop {
    animation-iteration-count: infinite;
    animation-name: drop;
    animation-timing-function: linear;
    height: 10px;
    position: absolute;
    border-left: 1px solid var(--rain-color);
}

@keyframes drop {
    100% {
        transform: translateY(85vh);
    }
}

.wave {
    position: absolute;
    opacity: 0;
    border: #FFF 2px solid;
    border-radius: 50%;
    animation-name: ripple;
    animation-iteration-count: infinite;
    width: 30px;
    height: 30px;
    filter: blur(2px);
    transform: perspective(15vh) rotateX(285deg);
}

@keyframes ripple {
    from {
        opacity: 0.7;
        margin-left: -15px;
        margin-top: -15px;
    }
    to {
        width: 60px;
        margin-left: -30px;
        margin-top: -30px;
        height: 60px;
        border-width: 1px;
        opacity: 0;
    }
}
