.hobby-section {
    z-index: 10;
    width: 100vw;
    height: 100vh;
    background: black;
    font-family: "andes";
    color: white;
    text-align: center;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    text-shadow: 0 0 10px white, 0px 0px 20px var(--neon-text-glow);
    user-select: none;
}

.hobby-return-button {
    position: relative;
    opacity: 0.3;
    width: 20vw;
    height: 100vh;
    font-size: 2em;
    padding-top: 25%;
    right: 0;
    cursor: pointer;
    background: linear-gradient(90deg, transparent 50%, var(--rapture-sea-green) 100%);
}
.hobby-return-button:hover {
    opacity: 1;
}