body { 
    overflow: hidden; 
    background-color: black; 
    scroll-behavior: smooth; 
}
body::-webkit-scrollbar { display: none; }

.pf-container {
    background: linear-gradient(180deg, rgba(0,0,0,1) 0%, #136087 100%);
    width: 100vw;
    height: 200vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    animation: load-page 0.5s ease;
}

@keyframes load-page {
    from { opacity: 0; }
    to { opacity: 1; }
}