.pf-link {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    z-index: 3;
    right: 0;
    animation: fade-in 1s ease; 
}

@keyframes fade-in {
    from { right: -20% };
    to { right: 0% };
}

.nav-icon-container {
    position: relative;
    margin-right: 20px;
    top: 20px;
    opacity: 0.4;
}
.nav-icon-container div[class*=-border] {
    border: 5px solid white;
    position: absolute;
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    top: 5px;
    left: 5px;
}
.nav-icon-container div[class*=-container] {
    width: 3em;
    height: 3em;
    cursor: pointer;
    position: relative;
}

.link-icon { margin: 10px; }

.linkedin-border { border-radius: 5px; }
.linkedin-container { border-radius: 10%; }
.linkedin-i, .linkedin-n {
    height: 45%;
    width: 15%;
    bottom: 15%; 
    left: 15%;
}
.linkedin-n { left: 40%; }
.linkedin-i, .linkedin-n, .linkedin-i::before, .linkedin-n::before, .linkedin-n::after {
    position: absolute;
    background: #fff;
}
.linkedin-i::before {
    height: 40%;
    width: 120%;
    content: "";
    border-radius: 50%;
    bottom: 115%;
    left: -10%;
}
.linkedin-n::before, .linkedin-n::after {
    bottom: 0;
    content: "";
    border-radius: 5em 5em 0 0;
    height: 100%;
    width: 224%;
    left: 76.7%;
}
.linkedin-n::after {
    background: black;
    height: 77.7%;
    width: 110%;
    left: 100%;
}

/*
    Modified version of github.com/bchanx's pure css logo for personal use.
 */
 
.github-border {
    border-radius: 50%;
}
.github-container {
    overflow: hidden;
    border-radius: 50%;
}
.github-container div {
    position: absolute;
    background-color: #ffffff;
}
.github-container > .none {
    background-color: black;
}
.github-container div[class*=head], .github-container div[class*=tail] {
    border-radius: 100%;
}
.github-container div[class*=ear] {
    width: 18.4%;
    height: 18.4%;
    top: 24%;
    border-radius: 100% 0% 40% 60% / 40% 0% 100% 60%;
}
.github-body {
    width: 20%;
    height: 28.4%;
    left: 40%;
    bottom: 0;
    border-radius: 45% 45% 0 0;
    z-index: 3;
}
.github-body-l, .github-body-r {
    width: 8.4%;
    height: 8.4%;
    bottom: 0;
    left: 35%;
    z-index: 2;
}
.github-body-r {
    left: 57%;
}
.github-body-l.none, .github-body-r.none {
    width: 10%; 
    height: 11.7%;
    bottom: 2.4%;
    border-radius: 10% 50% 50% 55% / 10% 55% 40% 60%;
    z-index: 2;
    left: 30%;
}
.github-body-r.none { 
    left: 60%; 
    transform: rotateY(180deg); 
}
.github-neck {
    width: 50%;
    height: 25%;
    left: 25%;
    top: 50%;
    border-radius: 50% 50% 50% 50% / 40% 40% 40% 40%;
    z-index: 3;
}
.github-head {
    width: 62.7%;
    height: 46.7%;
    left: 18.7%;
    top: 25%;
}
.github-head-l, .github-head-r {
    width: 41.7%;
    height: 45%;
    left: 18%;
    top: 28%;
    z-index: 3;
}
.github-head-r {
    left: 40.4%;
}
.github-ear-l {
    left: 61%;
    transform: rotateZ(350deg)
}
.github-ear-r {
    left: 21%;
    transform: rotateZ(280deg);
}
.github-tail-e {
    transform: rotate(-35deg);
    width: 5.4%;
    height: 11.7%;
    left: 17.7%;
    top: 70.4%;
}
.github-tail-b {
    width: 26%;
    height: 16%;
    left: 21%;
    top: 71%;
    z-index: 2;
}
.github-tail-e.none {
    transform: rotate(-24deg);
    width: 5.7%;
    height: 16%;
    left: 15.4%;
    top: 72.7%;
}
.github-tail-b.none {
    width: 22.4%;
    height: 16%;
    left: 22.4%;
    top: 64.7%;
    z-index: 2;
}
.github-tail-t {
    transform: rotate(12deg);
    width: 6.7%;
    height: 3%;
    left: 13.7%;
    top: 70.7%;
}

.resume-border { border-radius: 10px; }
.resume-flip {
    border-top: 5px solid black;
    border-right: 5px solid black;
    border-left: 5px solid white;
    border-bottom: 5px solid white;   
    width: 20%;
    height: 15%;
    position: absolute;
    right: 5px;
    top: 5px;
    background: black;
    border-radius: 5px;
}
.resume-lines {
    height: 40%;
    width: 70%;
    position: absolute;
    bottom: 15%;
    left: 15%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.resume-line {
    background: white;
    height: 10%;
    border-radius: 2px;
}
.resume-strike {
    width: 200%;
    height: 5px;
    background: white;
    position: absolute;
    right: -44.8%;
    transform: rotate(41deg);
    top: 30%;
}

.about-container {
    bottom: 5px;
    right: 5px;
}
.about-roof-house {
    border-top: 5px solid white;
    width: 50%;
    height: 25%;
    margin-left: calc(25% + 5px);
}
.about-base-house {
    border-bottom: 5px solid white;
    border-left: 5px solid white;
    border-right: 5px solid white;
    width: 100%;
    height: 75%;
    border-radius: 0 0 5px 5px;
}
.about-roof-l, .about-roof-r, .about-head, .about-body {
    position: absolute;
    background: white;
}
.about-roof-l, .about-roof-r {
    width: 50%;
    height: 5px;
    transform: rotate(-45deg);
    top: 16%;
    left: -4%;
}
.about-roof-r {
    transform: rotate(45deg);
    left: 71%;
}
.about-head {
    width: 30%;
    height: 30%;
    border-radius: 50%;
    bottom: 40%;
    left: 45%;
}
.about-body {
    width: 40%;
    height: 50%;
    border-radius: 5px;
    bottom: -15%;
    left: 40%;
}

.phone-container { border-radius: 10%; }
.phone-border { border-radius: 50%; }
.phone-border::before, .phone-border::after {
	content: "";
    bottom: -40%;
    left: -40%;
    border-width: 15px;
    border-style: solid;
    border-color: transparent #fff transparent transparent;
    position: absolute;
    transform: rotate(-45deg) rotateX(60deg);
}
.phone-border::after {
    bottom: calc(-40% + 5px);
    left: calc(-40% + 5px);
    border-color: transparent black transparent transparent;
}
.phone-holder {
    width: 70%;
    height: 25%;
    transform: rotate(45deg);
    background: white;
    left: 7%;
    position: absolute;
    top: 45%;
    border-radius: 0% 0% 45% 45% / 0% 0% 100% 100%;
}
.phone-holder-none {
    width: 40%;
    height: 35%;
    transform: rotate(45deg);
    background: black;
    left: 34%;
    position: absolute;
    top: 28%;
    border-radius: 0% 0% 15% 15% / 0% 0% 100% 100%;
}
.phone-ear, .phone-mouth {
    width: 20%;
    height: 17%;
    background: white;
    left: 20%;
    position: absolute;
    top: 25%;
    border-radius: 35% 15% 0% 60%;
    transform: rotate(45deg) skewY(15deg);
}
.phone-mouth {
    left: 56.5%;
    top: 59.5%;
    border-radius: 15% 35% 60% 0%;
    transform: rotate(45deg) skewY(-15deg);
}

.email-border { border-radius: 10%; }
.email-container { border-radius: 10%; }
.email-shape {
    left: 7.5px;
    top: calc(7.5% + 7.5px);
    background-color: #fff;
    position: absolute;
    height: calc(85% - 15px);
    width: calc(100% - 15px);
    overflow: hidden;
    border-radius: 10%;
}
.email-shape::before, .email-shape::after {
    content: "";
    position: absolute;
    transform: rotate(45deg) skew(345deg, 345deg);
    border-radius: 10%;
    width: 110%;
    background: #fff;
    padding-bottom: 110%;
    bottom: -90%;
    left: -2px;
    box-shadow: -2px -2px 0 0 black;
}
.email-shape::after {
    bottom: 45%;
    left: -2px;
    box-shadow: 2px 2px 0 0 black;
}

.popup-modal {
    position: absolute;
    left: -10vw;
    top: 3em;
    color: white;
    user-select: none;
    font-size: 0.6em;
    font-family: market-deco;
    animation: show-modal 2.5s linear;
    opacity: 0;
}

@keyframes show-modal {
    0% {
        opacity: 0;
    }
    10% {
        opacity: 1;
    }
    90% {
        opacity: 1;
    }
}