
.fishes-container {
    position: absolute;
    width: 100vw;
    height: 100vh;
}
.fish-school {
    position: absolute;
    left: -2%;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}
.fish {
    width: 1vw;
    height: 1vh;
    border-radius: 50%;
    background: white;
    position: absolute;
}
.fish::after {
    content: '';
    background: white;
    width: 45%;
    height: 150%;
    position: absolute;
    right: 35%;
    top: -25%;
    border-radius: 0% 100% 75% 25% / 0% 50% 50% 100% ;
}
.fish::before {
    content: '';
    background: white;
    width: 25%;
    height: 100%;
    position: absolute;
    left: -15%;
    border-radius: 0% 100% 100% 0% / 0% 50% 50% 100%;
}
  
@keyframes swim {
    0% { margin-left: -5%; }
    100% { margin-left: 105%; }
}

@keyframes swim-reverse {
    0% { margin-left: 105%; }
    100% { margin-left: -5%; }
}