:root {
  --neon-text-glow: #3bb5f2;
}

.neon-letter-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  color: #e1f5ff;
  text-shadow:
    0 -40px 100px #fff,
    0 0 2px, 0 0 1em var(--neon-text-glow),
    0 0 0.5em var(--neon-text-glow),
    0 0 0.1em var(--neon-text-glow),
    0 10px 3px #000;
  font-family: "andes";
  font-size: 3em;
  text-align: center;
  user-select: none;
  line-height: calc(100vh - 1em);
}

.neon-reflection {
  display: block;
  position: absolute;
  top: 1.3em;
  transform: rotateZ(180deg) rotateY(180deg) perspective(10vh) rotateX(-25deg); 
  filter: blur(2px);
}

@keyframes blink {
  78%,
  81%,
  83%,
  92.5% {
    color: inherit;
    text-shadow: inherit;
  }
  79% {
    color: #333;
  }

  80% {
    text-shadow: none;
  }
  82%,
  92% {
    color: #333;
    text-shadow: none;
  }
}